import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./FacilityCarousel.scss";
import { useNavigate } from "react-router-dom";

interface Slide {
  src: string;
  alt: string;
  title: string;
  link: string;
}

const slides: Slide[] = [
  {
    src: `${process.env.PUBLIC_URL}/facilityImage/image1.png`,
    alt: " ",
    title: " ",
    link: `/facilities/1st/funeral-office`,
  },
  {
    src: `${process.env.PUBLIC_URL}/facilityImage/image2.png`,
    alt: "",
    title: "",
    link: `/facilities/1st/preparation-room`,
  },
  {
    src: `${process.env.PUBLIC_URL}/facilityImage/image3.png`,
    alt: "",
    title: "",
    link: `/facilities/1st/office`,
  },
  {
    src: `${process.env.PUBLIC_URL}/facilityImage/image4.png`,
    alt: " ",
    title: " ",
    link: `/facilities/1st/reception`,
  },
  {
    src: `${process.env.PUBLIC_URL}/facilityImage/image5.png`,
    alt: " ",
    title: " ",
    link: `/facilities/1st/family-waiting-room`,
  },
  {
    src: `${process.env.PUBLIC_URL}/facilityImage/image6.png`,
    alt: "",
    title: "",
    link: `/facilities/2nd/mourning-room`,
  },
  {
    src: `${process.env.PUBLIC_URL}/facilityImage/image7.png`,
    alt: " ",
    title: " ",
    link: `/facilities/2nd/family-lounge`,
  },
  {
    src: `${process.env.PUBLIC_URL}/facilityImage/image8.png`,
    alt: ", ",
    title: ", ",
    link: `/facilities/2nd/restaurant`,
  },
  {
    src: `${process.env.PUBLIC_URL}/facilityImage/image9.png`,
    alt: "",
    title: "",
    link: `/facilities/2nd/cafe`,
  },
];

const settings = {
  infinite: true,
  speed: 500,
  slidesToShow: 4,
  slidesToScroll: 1,
  arrows: true,
  centerMode: false,
  initialSlide: 0,
  variableWidth: true,
};

const FacilityCarousel: React.FC = () => {
  const handleSlideClick = (link: string) => {
    window.location.href = link;
  };
  const navigate = useNavigate();

  return (
    <div className="facility-carousel">
      <div className="subTitle">   </div>
      <div className="mainTitle">   </div>
      <Slider {...settings}>
        {slides.map((slide, index) => (
          <div
            key={index}
            className="facilityItem"
            onClick={() => navigate(slide.link)}
          >
            <img className="facilityImage" src={slide.src} alt={slide.alt} />
            <p className="title">{slide.title}</p>
          </div>
        ))}
      </Slider>
    </div>
  );
};

export default FacilityCarousel;
